@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i');
body{
    font-family: 'Roboto', sans-serif;
    font-weight: normal
}
a, a:hover, a:focus {color: #565656}
.print_wraper{
    max-width: 12in;
}
.print_wraper .modal-body{
    padding: 10px 0.3in;
}
.print_wraper .modal-body{
    /* max-height: 600px !important; */
    overflow: hidden;
    overflow-y: hidden;
    overflow-y: auto;
}
.table-header-main{
    word-wrap: break-word;
    font-style: normal;
    font-family: 'Roboto', sans-serif;
    font-size: 11pt;
    font-weight: 400;
    text-decoration: none;
    color: #424141;
    direction: ltr;
    unicode-bidi: normal;
    -webkit-writing-mode: lr-tb;
        -ms-writing-mode: lr-tb;
            writing-mode: lr-tb;
    layout-flow: horizontal;
    vertical-align: top;
    padding-left: 5pt;
    padding-top: 1pt;
    padding-right: 2pt;
    padding-bottom: 1pt;}

    table.table-header-main td, table.table-header-main th {
        width: 1%;
        font-size: 11pt;
        vertical-align: top;
        font-weight: 400;
        text-decoration: none;
        padding-left: 5pt;
        padding-top: 1pt;
        padding-right: 2pt;
        padding-bottom: 1pt;
        font-weight: inherit;
        color: inherit;
        /* line-height: 1.6; */
    }
table.print_table td, table.print_table th {
    width: 1%;
    font-size: 11pt;
    vertical-align: top;
    font-weight: 400;
    text-decoration: none;
}

table.print_table{font-style: normal;font-size: 11pt;}
table.print_table td{color: #424141;}
table.print_table th{color: #333;font-weight: bold;}


.table .thead-dark th{
    background-color: #f5f5f5;
    border-color: #f5f5f5;
    font-size: 11.5pt;
    font-weight: bold;
    color:#333;
    vertical-align: middle;
}
    .table_title{
        /* background-color: #549bce;color: White; */
        color: #333;
        background-color: #f5f5f5;
        border-color: #f5f5f5;
        font-weight: bold;
    }
    .table_header{
      word-wrap: break-word;
      font-style: normal;
      font-size: 11pt;
      font-weight: 700;
      color: #333;
      }
      table.table-header-main .table_header th{
          font-size: 12pt;
          padding-bottom: 5px;
        padding-top: 15px;
    }
      .table-header-main th{padding: 5px 5px;}
      .table-header-main tbody tr{
          word-wrap: break-word;
        font-style: normal;
        font-family: 'Roboto', sans-serif;
        font-size: 11pt;
        font-weight: 400;
        text-decoration: none;
        color: #333;
        vertical-align: top;
        padding: 2px 2px;
     }
     .table_border td{border-bottom: 1px solid #ccc;padding-top: 5px !important}
     .table_border + tr td{padding-top: 6px !important}

     .table_space td{padding-top: 5px !important}
     .table_space + tr td{padding-top: 6px !important}


    /* .table_body_thirdrow{color: #fff !important;
        background-color: #f5f5f5;
        border-color: #f5f5f5;} */
    .table_body_thirdrow:last-child td{padding-bottom: 7px;}

    
  
.hiddentable{
    opacity: 0;
    height:0;
    overflow: hidden;
}

.btn-info{
    color: #fff;
    background-color: #333;
    border-color: #333;
    
}
.btn-info:hover{
    background-color: #333;
    border-color: #333;
}

.btn-primary{
    background-color: #ff7c2c;
    border-color: #ff7c2c;
}
.btn-primary:hover{
    color: #fff;
    background-color: #fc5a0a;
    border-color: #fc5a0a;
}
.btn i{margin-right: 5px;}
.responsive {
    width: 100%;
    height: auto;
  }


  /* borderless css */
  .table-bordered td, .table-bordered th{
      border-right: 0px;
      border-left: 0px;
  }
  .table-bordered td:last-child, .table-bordered th:last-child{
    /* border-right: 1px solid #dee2e6; */
}

.table_logo th {
    /* border-top: 1px solid #dee2e6 !important; */
    background: #fff !important;
}
.thead-dark th{
    border-top: 1px solid #dee2e6 !important;
    border-bottom: 1px solid #dee2e6 !important;
}
tfoot tr:not(.footerrow) td {
    background: #f5f5f5;
    /* border: none !important; */
    padding: 7px 12px !important;
    vertical-align: middle
}
/* tfoot .footerrow td {
    border: 1px solid #dee2e6 !important;
} */

  .print_wraper .print_table tr td:first-child, .print_wraper .print_table tr th:first-child{padding: 10px 5px;}
  .footerrow td{line-height: 2}
  .table .btn {box-shadow: none ;-webkit-box-shadow: none; color: #ff7c2c;font-size: 11pt;}

  table.print_table tbody tr:hover td {background-color: #f5f5f5;
    /* border-color: #f5f5f5;  */
}

    .table .btn:hover{color:#fc5a0a;text-decoration: underline}

    .print_icon .btn-primary{border: 1px solid #dad9d9;
        border-radius: 5px;
        display: inline-block;
        margin-left: 4px;
        vertical-align: text-top;
        padding: 6px 8px;
        position: relative;
        background-color: transparent;
        color: #7c7a7a;
        width: 50px;
    height: 45px;
    }
 .print_icon .btn-primary:hover{background-color: #eee;border-color: #dad9d9;color: #333;}
.print_icon .btn i{margin-right: 0px;font-size: 24px;}
    .modal .close{font-size: 30px;}

    .dt_cp{text-transform: capitalize}

    .table_inner tr td, .table_inner tr th{padding: 0px;}

    tfoot tr.wthoubg td{background:#fff;}
    tfoot tr.wthoubg td + td{border-left: 1px solid #dee2e6;}
    .dropdown-header{color: #000;font-size: 16px;}
    .nopadding {
        padding: 0;
    }
    .nopadding_r {
        padding-right: 0;
    }
    .nopadding_l {
        padding-left: 0;
    }
.RegisterInner {
        border-bottom: 0px;
        padding-bottom: 1 5px;
        border: 1px solid #d9d8d9;
            border-bottom-color: rgb(217, 216, 217);
            border-bottom-style: solid;
            border-bottom-width: 1px;
    }
   .RegisterInner h3 {
        margin: 0;
            margin-bottom: 0px;
        padding: 12px 0;
        font-size: 18px;
        position: relative;
        background: #f1f1f1;
        margin-bottom: 15px;
        text-transform: capitalize;
        border-bottom: 1px solid #d9d8d9;
    }
    .RegisterInner .SubHeading {
        font-size: 20px;
        font-weight: 500;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.5px;
    }
    .RegisterInner label {
        text-align: right;
        padding-top: 6px;        
    }
    .searchBtn {
        padding: 6px 35px;
        letter-spacing: 0.5px;
        text-transform:uppercase;
    }

    .react-datepicker__input-container {
        position: relative;
        display: -ms-flexbox;
        display: flex;
    }
    .react-datepicker__input-container input[name~="startDate"],  .react-datepicker__input-container input[name~="endDate"]{
        padding-right: 30px;
        background: transparent;
    }
    .react-datepicker__input-container:before{
        content: "\F073";
        font-family: "Font Awesome 5 Free";
        position: absolute;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-item-align: center;
            align-self: center;
        right: 7px;
        z-index: -1;
      }

    .serch_form .form-control{font-size: 14px;}


    .has-error{
        border-color: #a94442;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
                box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    }
    .print_table tr td.wrapedcolumn{word-break: break-all;min-width: 180px !important}
    .form-control:focus, .btn:focus{-webkit-box-shadow: none !important;box-shadow: none !important}
    button:focus{outline: none !important}
    
    @media print {
        /* Your styles here */
        .print-body{margin: 0.3in;}
    }



